import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useHistory } from "react-router-dom";
import axiosRetry from 'axios-retry';
import VerticalBarChart from '../../components/bar-chart-vertical';
import HorizontalBarChart from '../../components/bar-chart-horizontal';
import { API_BASE_URL } from '../../constants/config';
import PageContainer from '../../components/page-container/PageContainer';

const BarChart = () => {

  const [loading, setLoading] = useState(false);
  const [data, setData] = useState();

  let pathname = useHistory();
  let region = pathname.location.search.split("region=")[1];

  console.log(region)

  const fetchData = async () => {
    // let url = `${API_BASE_URL}/amcharts/simple-column-chart`;
    let temp_base_url = `http://ec2-18-221-128-59.us-east-2.compute.amazonaws.com:8000`;
    let parent_route = `/amcharts/simple-column-chart`;
    let url = temp_base_url + parent_route + 
    (region ? `?region=${region}` : '');

    setLoading(true);
    axiosRetry(axios, { retries: 3 });

    await axios({
      method: 'GET',
      url: url
    }).then(result => {
      let data = result.data;
      console.log(data)
      setData(data)
      setLoading(false);
    }).catch(error => {
      console.log(error);
      setData();
      setLoading(false);
    });
  };

  useEffect(() => {
    fetchData();
  }, []);

  return (

    <PageContainer loading={loading}>
      <h1>Hi I am bar chart page</h1>
      {/* <pre>{JSON.stringify(data, null, 2)}</pre> */}

      <div className="chart-container">
        <h3>This is veritcal bar chart</h3>
        {data
          ? data.data.length > 0
            ? <>
              <VerticalBarChart
                id={data.properties._id}
                categoryX={data.properties.categoryX}
                valueY={data.properties.valueY}
                seriesName={data.properties.seriesName}
                data={data.data}
              />
            </>
            : <p>No data found!</p>
          : <p>No data found!</p>
        }
      </div>
    </PageContainer>
  );
}

export default BarChart;